<template>
  <header>
    <div class="header-content">
      <div class="logo">
        <router-link to="/">
          <h1 class="font-fantasy">BC</h1>
        </router-link>
      </div>

      <div class="menu-switcher"
           @click="toggleMenu">
        <template v-if="!menuVisible">
          <z-icon name="menu-open"/>
        </template>

        <template v-if="menuVisible">
          <z-icon name="menu-close"/>
        </template>
      </div>

      <div class="social-links">
        <ul>
          <li v-for="(social, index) in socialLinks"
              :key="index">
            <a :href="social.link" target="_blank">
              <z-icon :name="social.icon"/>
            </a>
          </li>
        </ul>
      </div>
    </div>

    <transition name="menu">
      <nav v-show="menuVisible"
           class="menu"
           @click="hideMenu">
        <ul>
          <li v-for="(route, index) in $router.options.routes"
              :key="index">
            <router-link :to="route.path">
              <h2 class="font-fantasy">
                {{ route.meta.displayName }}
              </h2>
              <h6>
                {{ route.meta.description }}
              </h6>
            </router-link>
          </li>
        </ul>
      </nav>
    </transition>
  </header>
</template>

<script>
import ZIcon from '@/components/ZIcon'

export default {
  name: 'ZHeader',
  components: {
    ZIcon
  },
  data () {
    return {
      menuVisible: false,
      socialLinks: [
        {
          icon: 'github',
          link: 'https://github.com/benavern'
        },
        {
          icon: 'twitter',
          link: 'https://twitter.com/benavern'
        },
        {
          icon: 'linkedin',
          link: 'https://www.linkedin.com/in/benjamincaradeuc'
        }
      ]
    }
  },
  methods: {
    toggleMenu () {
      this.menuVisible = !this.menuVisible
    },
    hideMenu () {
      this.menuVisible = false
    }
  }
}
</script>

<style lang="sass">
  #content
    margin-left: $menu-width-desktop

    @media (max-width: $medium)
      margin-left: 0
      margin-top: 5rem
</style>

<style lang="sass" scoped>
  header
    position: fixed
    left: 0
    top: 0
    height: 100vh
    width: $menu-width-desktop
    background-color: $white
    border-right: 1px solid $gray-light
    box-shadow: 0 0 .5rem $gray-light
    z-index: 999

    @media (max-width: $medium)
      height: $menu-height-mobile
      width: 100%
      border-right: none
      border-bottom: 1px solid $gray-light

    .header-content
      height: 100%
      width: 100%
      display: flex
      flex-direction: column
      justify-content: space-between
      text-align: center

      @media (max-width: $medium)
        flex-direction: row

      .logo
        a
          color: $black
          h1
            font-weight: normal
            margin: 0
            line-height: $menu-height-mobile
            padding: .5rem 1rem

      .menu-switcher
        font-size: 2rem
        padding: 1rem
        cursor: pointer

        &:hover
          color: $primary

      .social-links

        @media (max-width: $medium)
          display: none

        ul
          list-style: none
          padding: 1rem 0
          font-size: 1.2rem

          li a
            display: block
            padding: .2em

    .menu
      position: absolute
      top: 0
      left: $menu-width-desktop
      width: calc(100vw - #{$menu-width-desktop})
      height: 100%
      background: rgba($black, .3)

      @media (max-width: $medium)
        width: 100%
        left: 0
        top: $menu-height-mobile
        height: calc(100vh - #{$menu-height-mobile})

      ul
        height: 100%
        list-style: none
        display: flex
        flex-direction: column
        position: relative

        li
          flex: 1
          border-top: 1px solid $gray-light
          display: flex
          background-color: $white
          transition: transform .5s

          &:first-child
            border-top: none

          a
            padding: 1rem
            flex: 1
            display: flex
            flex-direction: column
            align-items: center
            justify-content: center

            h2
              color: $black
            h6
              color: $gray

            &:hover
              color: $primary
              background-color: $gray-lighter

              h2, h6
                color: $primary

      ////// transition settings //////
      &.menu-enter-active,
      &.menu-leave-active
        transition: opacity .3s

        li:nth-child(even)
          transform: translateX(-100%)

        li:nth-child(odd)
          transform: translateX(100%)

      &.menu-enter,
      &.menu-leave-to
        opacity: 0

        li
          transform: translateX(0)
      /////// END transition settings ///////

</style>



